const state = {
    methods: [],  //
    areaList: [], //
    areaCode: '' //id
}

const mutations = {
    setXtMethod(state, methods) {
        state.methods = methods;
    },
    setAreaList(state, list) {
        state.areaList = list;
    },
    setAreaCode(state, code) {
        state.areaCode = code;
    }
}
// 
export default {
    state,
    mutations
}
